import WEUI from 'react-weui';
import React, { PropTypes } from 'react';
import withStyles from 'isomorphic-style-loader/lib/withStyles';

import IconAddfriend from './assets/icon_addfriend.png';
import IconPublic from './assets/icon_public.png';
import IconQuanlian from './assets/icon_qunliao.png';

import s from './Contact.css';
import SampleData from '../home/nameDB';

const smallIcon = <img className={s.icon} alt="" src="data:image/png;base64," />;


const {
  SearchBar,
  Panel,
  PanelHeader,
  PanelBody,
  MediaBox,
  Cell,
  CellBody,
  Cells,
  CellHeader,
} = WEUI;

class Contact extends React.Component {

  static propTypes = {
    friends: PropTypes.arrayOf(PropTypes.shape({
      title: PropTypes.string.isRequired,
      contentSnippet: PropTypes.string,
    })).isRequired,
  };

  state = {
    searchText: '',
    results: [],
  };

  handleChange(text) {
    const keywords = [text];
    let results = SampleData.filter(/./.test.bind(new RegExp(keywords.join('|'), 'i')));

    if (results.length > 3) results = results.slice(0, 3);
    this.setState({
      results,
      searchText: text,
    });
  }

  render() {
    return (
      <div>
        <SearchBar
          onChange={this.handleChange.bind(this)}
          placeholder="Search"
          lang={{
            cancel: 'Cancel',
          }}
        />
        <Panel style={{display: this.state.searchText ? 'none' : null, marginTop: 0}}>
          <PanelBody>
            <MediaBox type="small_appmsg">
              <Cells>
                <Cell href="javascript:;" access>
                  <CellHeader><img className={s.icon} alt="" src={IconAddfriend}/></CellHeader>
                  <CellBody>
                    <p>新的朋友</p>
                  </CellBody>
                </Cell>
              </Cells>
            </MediaBox>
            <MediaBox type="small_appmsg">
              <Cells>
                <Cell href="javascript:;" access>
                  <CellHeader><img className={s.icon} src={IconQuanlian}/></CellHeader>
                  <CellBody>
                    <p>群聊</p>
                  </CellBody>
                </Cell>
              </Cells>
            </MediaBox>
            <MediaBox type="small_appmsg">
              <Cells>
                <Cell href="javascript:;" access>
                  <CellHeader><img className={s.icon} src={IconPublic}/></CellHeader>
                  <CellBody>
                    <p>公众号</p>
                  </CellBody>
                </Cell>
              </Cells>
            </MediaBox>
          </PanelBody>
        </Panel>
        <Panel style={{display: this.state.searchText ? 'none' : null, marginTop: 0}}>
          <PanelHeader>
            Z
          </PanelHeader>
          <PanelBody>
            {
              this.props.friends.length > 0 ?
                this.props.friends.map((item, i)=> {
                  return (
                    <MediaBox type="small_appmsg">
                      <Cells>
                        <Cell href="javascript:;" access>
                          <CellHeader>{smallIcon}</CellHeader>
                          <CellBody>
                            <p>{item.title}</p>
                          </CellBody>
                        </Cell>
                      </Cells>
                    </MediaBox>
                  )
                })
                : <MediaBox>Can't find any！</MediaBox>
            }
          </PanelBody>
        </Panel>
        <Panel style={{display: this.state.searchText ? null : 'none', marginTop: 0}}>
          <PanelHeader>
            Female Name Search
          </PanelHeader>
          <PanelBody>
            {
              this.state.results.length > 0 ?
                this.state.results.map((item, i)=> {
                  return (
                    <MediaBox type="small_appmsg">
                      <Cells>
                        <Cell href="javascript:;" access>
                          <CellHeader>{smallIcon}</CellHeader>
                          <CellBody>
                            <p>{item}</p>
                          </CellBody>
                        </Cell>
                      </Cells>
                    </MediaBox>
                  );
                })
                : <MediaBox>Can't find any！</MediaBox>
            }
          </PanelBody>
        </Panel>
      </div>
    );
  }
}

export default withStyles(s)(Contact);



