import React, { PropTypes } from 'react';
import withStyles from 'isomorphic-style-loader/lib/withStyles';
import s from './Home.css';
import SampleData from './nameDB';

const appMsgIcon = <img src="data:image/png;base64," />

import WEUI from 'react-weui';
const {
  SearchBar,
  Panel,
  PanelHeader,
  PanelBody,
  MediaBox,
  MediaBoxBody,
  MediaBoxTitle,
  MediaBoxDescription,
  PanelFooter,
  MediaBoxHeader,
  Cell,
  CellBody,
  CellFooter
} = WEUI;

const CellMore = () => (
  <Cell access link>
    <CellBody>More</CellBody>
    <CellFooter />
  </Cell>
)


class Home extends React.Component {
  state={
    searchText: '',
    results: []
  };

  static propTypes = {
    news: PropTypes.arrayOf(PropTypes.shape({
      title: PropTypes.string.isRequired,
      contentSnippet: PropTypes.string,
    })).isRequired,
  };

  handleChange(text, e){
    let keywords = [text];
    let results = SampleData.filter(/./.test.bind(new RegExp(keywords.join('|'),'i')));

    if(results.length > 3) results = results.slice(0,3);
    this.setState({
      results,
      searchText:text,
    });
  }

  render() {
    return (
      <div>
        <SearchBar
          onChange={this.handleChange.bind(this)}
          placeholder="Search"
          lang={{
            cancel: 'Cancel'
          }}
        />
        <Panel style={{display: this.state.searchText ? 'none': null, marginTop: 0}}>
          <PanelBody>
            {
              this.props.news.length > 0 ?
                this.props.news.map((item,i)=>{
                  return (
                    <MediaBox key={i} type="appmsg" href="javascript:void(0);">
                      <MediaBoxHeader>{appMsgIcon}</MediaBoxHeader>
                      <MediaBoxBody>
                        <MediaBoxTitle>{item.title}</MediaBoxTitle>
                        <MediaBoxDescription>
                          {item.description}
                        </MediaBoxDescription>
                      </MediaBoxBody>
                    </MediaBox>
                  )
                })
                : <MediaBox>Can't find any！</MediaBox>
            }
          </PanelBody>
        </Panel>
        <Panel style={{display: this.state.searchText ? null: 'none', marginTop: 0}}>
          <PanelHeader>
            Female Name Search
          </PanelHeader>
          <PanelBody>
            {
              this.state.results.length > 0 ?
                this.state.results.map((item,i)=>{
                  return (
                    <MediaBox key={i} type="appmsg" href="javascript:void(0);">
                      <MediaBoxHeader>{appMsgIcon}</MediaBoxHeader>
                      <MediaBoxBody>
                        <MediaBoxTitle>{item}</MediaBoxTitle>
                        <MediaBoxDescription>
                          You may like this name.
                        </MediaBoxDescription>
                      </MediaBoxBody>
                    </MediaBox>
                  )
                })
                : <MediaBox>Can't find any！</MediaBox>
            }
          </PanelBody>
          <PanelFooter href="javascript:void(0);">
            <CellMore />
          </PanelFooter>
        </Panel>
      </div>
    );
  }
}

export default withStyles(s)(Home);



